* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}
button,
html [type='button'],
[type='reset'],
[type='submit'] {
  -webkit-appearance: button;
}
button:focus {
  outline: none;
}
html {
  height: 100%;
}
body {
  margin: 0;
  height: 100%;
  font-size: 14px;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
button,
p {
  padding: 0;
  margin: 0;
  font-weight: normal;
}
input,
textarea {
  margin: 0;
  padding: 0;
  outline: none;
}
ol,
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}
a:active,
a:focus,
a:hover {
  text-decoration: none;
}
.clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.f-r {
  float: right;
}
.f-l {
  float: left;
}
.dsn {
  display: none;
}
.ds-lb {
  display: inline-block;
}
.w-300 {
  width: 300px;
}
.w-320 {
  width: 320px;
}
.w-200 {
  width: 200px;
}
.w-400 {
  width: 400px;
}
.w-500 {
  width: 500px;
}
.ta-r {
  text-align: right;
}
.ta-l {
  text-align: left;
}
.ta-c {
  text-align: center;
}
.fs-12 {
  font-size: 12px;
}
.fs-14 {
  font-size: 14px;
}
.fs-16 {
  font-size: 16px;
}
.fs-18 {
  font-size: 18px;
}
.fs-20 {
  font-size: 20px;
}
.ml-5 {
  margin-left: 5px;
}
.mr-10 {
  margin-right: 10px;
}
.ml-10 {
  margin-left: 10px;
}
.mb-10 {
  margin-bottom: 10px;
}
.mt-10 {
  margin-top: 10px;
}
.mr-20 {
  margin-right: 20px;
}
.ml-20 {
  margin-left: 20px;
}
.mb-20 {
  margin-bottom: 20px;
}
.mt-20 {
  margin-top: 20px;
}
.pr-10 {
  padding-right: 10px;
}
.pl-10 {
  padding-left: 10px;
}
.pb-10 {
  padding-bottom: 10px;
}
.pt-10 {
  padding-top: 10px;
}
.pr-20 {
  padding-right: 20px;
}
.pl-20 {
  padding-left: 20px;
}
.pb-40 {
  padding-bottom: 40px;
}
.pt-40 {
  padding-top: 40px;
}
.pad20 {
  padding: 20px;
}
.ver-m {
  vertical-align: middle;
}
.ver-tb {
  vertical-align: text-bottom;
}
.primary-color {
  color: #f8a03c;
}
.red {
  color: #f25252;
}
.cursor-pointer {
  cursor: pointer;
}
.ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.App {
  margin: 0;
  padding: 0;
  background: #f0f2f5;
  overflow-y: auto;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
